import React from 'react'
import { Container, Nav, Navbar } from 'react-bootstrap'
import styled from 'styled-components'

const Footer = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light bottom">
                <div className="container-fluid">
                    <a className="navbar-brand" href="#">TuPiel</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <a className="nav-link disabled" aria-current="page" href="#">Hecho por Sara Bermudez Alvarez</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link disabled" href="#pedirAhora">Copyright 2021</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
    )
}

export default Footer